module.exports = ":host {\n  --color-blue: #004adc;\n  --color-black: #222330;\n  --color-black-80: rgba(0, 0, 0, 0.8);\n  --color-black-60: rgba(0, 0, 0, 0.6);\n  --color-black-30: rgba(0, 0, 0, 0.3);\n  --color-black-10: rgba(0, 0, 0, 0.1);\n  --color-black-05: rgba(0, 0, 0, 0.05);\n  --color-spinner: rgba(0, 0, 0, 0.4);\n  --color-grey-light: #CCC;\n  --color-white: #FFF;\n  --align-buttons: left;\n  --height-ratio: 50%;\n  font-family: var(--font-family, sans-serif);\n}\n\nimg {\n  display: block;\n  height: auto !important;\n}\n\n* {\n  max-width: 100%;\n}\n\n.hidden {\n  display: none;\n}\n\n.ck-media {\n  width: 100%;\n  border: 1px solid var(--color-black-10);\n  position: relative;\n}\n\n.ck-media__placeholder {\n  width: 100%;\n  padding-bottom: var(--height-ratio);\n  background: repeating-linear-gradient(\n  45deg,\n  var(--color-white),\n  var(--color-white) 30px,\n  var(--color-black-05) 30px,\n  var(--color-black-05) 60px\n  );\n}\n\n.ck-media__preview {\n  width: 100%;\n  height: 100%;\n}\n\n.ck-media__loader {\n  width: 100%;\n  height: 100%;\n  position: absolute;\n  top: 0;\n  left: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n\nck-media-loader {\n  opacity: 1;\n  visibility: visible;\n  transition: all 0.2s ease;\n  pointer-events: none;\n}\n\n.ck-media__spinner {\n  width: 100px;\n  text-align: center;\n}\n\n.ck-media__spinner > .ck-media__bounce {\n  width: 16px;\n  height: 16px;\n  background-color: var(--color-spinner);\n\n  border-radius: 100%;\n  display: inline-block;\n  -webkit-animation: ck-bounce 1.4s infinite ease-in-out both;\n  animation: ck-bounce 1.4s infinite ease-in-out both;\n}\n\n.ck-media__spinner .ck-media__bounce--1 {\n  -webkit-animation-delay: -0.32s;\n  animation-delay: -0.32s;\n}\n\n.ck-media__spinner .ck-media__bounce--2 {\n  -webkit-animation-delay: -0.16s;\n  animation-delay: -0.16s;\n}\n\n@-webkit-keyframes ck-bounce {\n  0%, 80%, 100% { -webkit-transform: scale(0) }\n  40% { -webkit-transform: scale(1.0) }\n}\n\n@keyframes ck-bounce {\n  0%, 80%, 100% {\n    -webkit-transform: scale(0);\n    transform: scale(0);\n  } 40% {\n    -webkit-transform: scale(1.0);\n    transform: scale(1.0);\n  }\n}\n"