module.exports = ":host {\n  --button-size: 3em;\n  --button-background-color: black;\n  --button-foreground-color: white;\n  --outline-color: #004adc;\n  position: relative;\n  display: block;\n}\n\n.hovered .item {\n  outline-style: solid;\n  outline-width: 2px;\n  outline-color: var(--outline-color);\n}\n\n.controls {\n  z-index: 1;\n  position: absolute;\n  right: 0;\n  display: flex;\n  opacity: 0;\n  transition: opacity 0.2s ease;\n}\n\n.hovered .controls {\n  opacity: 1;\n}\n\n.controls button {\n  border: none;\n  background: var(--button-background-color);\n  width: var(--button-size);\n  height: var(--button-size);\n  display: flex;\n  cursor: pointer;\n}\n\nsvg {\n  width: 100%;\n}\nsvg * {\n  fill: var(--button-foreground-color);\n}\n.disabled {\n  opacity: 0.5;\n  pointer-events: none;\n  cursor: auto;\n}\n"