module.exports = ":host {\n  --color-blue: #004adc;\n  --color-black: #222330;\n  --color-black-80: rgba(0, 0, 0, 0.8);\n  --color-black-60: rgba(0, 0, 0, 0.6);\n  --color-black-30: rgba(0, 0, 0, 0.3);\n  --color-grey-light: #CCC;\n  --color-white: #FFF;\n  --font-size-text: 16px;\n  --baseline: 8px;\n  --baseline2: 16px;\n  --baseline3: 24px;\n  --baseline4: 32px;\n  --baseline5: 40px;\n  font-style: initial;\n  font-weight: initial;\n  font-family: var(--font-family, sans-serif);\n}\n\n.ck-tabs {\n  width: 100%;\n  display: inline-block;\n  margin: auto;\n  overflow: hidden;\n}\n\n.ck-tabs__header-tab-list {\n  list-style: none;\n  display: flex;\n  padding-left: 0;\n  border-bottom: 1px solid var(--color-grey-light);\n}\n\n\n.ck-tabs__header-tab-item {\n  padding: 0 var(--baseline3) 0 var(--baseline3);\n  border-bottom: 1px solid var(--color-grey-light);\n  margin-bottom: -1px;\n  cursor: pointer;\n  position: relative;\n  font-size: var(--font-size-text);\n  position: relative;\n}\n\n.ck-tabs__header-icon {\n  height: 0;\n  width: 0;\n  cursor: pointer;\n  opacity: 0;\n  visibility: hidden;\n  position: absolute;\n  top: 0;\n  right: 0;\n}\n\n.ck-tabs__header-tab-item.active {\n  border-bottom: 2px solid var(--color-blue);\n  color: var(--color-blue);\n  font-weight: bold;\n  cursor: default;\n}\n\n.ck-tabs__header-tab-item.active .ck-tabs__header-icon {\n  width: var(--baseline2);\n  height: var(--baseline2);\n}\n\n.ck-tabs__header-tab-item.active:hover .ck-tabs__header-icon {\n  opacity: 1;\n  visibility: visible;\n}\n\n.ck-tabs__header-tab-item.default:before {\n  content: '*';\n  margin-right: 5px;\n  font-size: 19px;\n  height: var(--baseline2);\n  position: absolute;\n  left: 5px;\n  top: 0;\n}\n\n.ck-tabs__header-tab-add {\n  margin-left: var(--baseline3);\n  cursor: pointer;\n  height: 20px;\n  width: 20px;\n  background-color: var(--color-black-80);\n  text-align: center;\n  color: var(--color-white);\n  border-radius: 50%;\n  transition: background-color 0.35s ease;\n  position: relative;\n  margin-bottom: 15px;\n}\n\n.ck-tabs__header-tab-add::before {\n  content: '+';\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -54%)\n}\n\n.ck-tabs__header-tab-add:hover {\n  background-color: var(--color-blue);\n}\n\n.ck-tabs__rail {\n  display: flex;\n}\n\n::slotted(*) {\n  display: block;\n  width: 100%;\n  flex-shrink: 0;\n}\n"