module.exports = ":host{\n  --color-light-green: rgba(91, 200, 156, 0.3);\n  --color-light-red: rgba(215, 34, 34, 0.3);\n  --color-green: #5bc89c;\n  --color-red: #d72222;\n  --highlight-padding: 0px;\n}\n\n.overlay {\n  position: relative;\n}\n\n.overlay.removed {\n}\n\n.overlay__background {\n  position: absolute;\n  border: 2px solid var(--color-green);\n  top: calc(var(--highlight-padding) * -1);\n  left: calc(var(--highlight-padding) * -1);\n  bottom: calc(var(--highlight-padding) * -1);\n  right: calc(var(--highlight-padding) * -1);\n  background: var(--color-light-green);\n  z-index: 5;\n}\n\n.removed .overlay__background {\n  background: var(--color-light-red);\n  border: 2px solid var(--color-red);\n}\n\n.actions {\n  visibility: hidden;\n  opacity: 0;\n  position: absolute;\n  left: 0;\n  right: 0;\n  top: 0;\n  bottom: 0;\n  width: 200px;\n  height: 20px;\n  margin: auto;\n}\n\n.overlay:hover .actions {\n  visibility: visible;\n  opacity: 1;\n  transition: visibility .35s linear, opacity .35s;\n}\n\nbutton {\n  border: none;\n  color: white;\n  cursor: pointer;\n  font-family: inherit;\n  font-size: inherit;\n  padding: 5px 10px;\n}\n\nbutton.accept {\n  background-color: var(--color-green);\n  margin-right: 20px;\n}\n\nbutton.decline {\n  background-color: var(--color-red);\n}\n"