module.exports = ":host {\n  --color-blue: #004adc;\n  --color-black: #222330;\n  --color-black-80: rgba(0, 0, 0, 0.8);\n  --color-black-60: rgba(0, 0, 0, 0.6);\n  --color-black-30: rgba(0, 0, 0, 0.3);\n  --color-grey-light: #CCC;\n  --color-white: #FFF;\n  --align-buttons: left;\n  font-family: var(--font-family, sans-serif);\n  font-style: initial;\n  font-weight: initial;\n  --gallery-position-top: auto;\n  --gallery-position-left: 50%;\n  --gallery-position-right: auto;\n  --gallery-position-bottom: 40px;\n  --gallery-transform: translate(-50%, 0);\n}\n\n.ck-gallery {\n  position: relative;\n  width: 100%;\n  display: inline-block;\n  margin: auto;\n  overflow: hidden;\n}\n\n.disabled {\n  opacity: 0.3;\n}\n\n.ck-gallery__rail {\n  display: flex;\n  transition: transform 0.7s ease;\n}\n\n::slotted(*) {\n  display: block;\n  width: 100%;\n  flex-shrink: 0;\n}\n\n.ck-gallery__controls {\n  position: absolute;\n  top: var(--gallery-position-top);\n  bottom: var(--gallery-position-bottom);\n  left: var(--gallery-position-left);\n  right: var(--gallery-position-right);\n  transform: var(--gallery-transform);\n  background-color: var(--color-white);\n  display: flex;\n  min-width: 40px;\n  max-height: 60px;\n  justify-content: space-between;\n  align-items: flex-end;\n  padding: 10px 20px;\n  border-radius: 10px;\n}\n\n.ck-gallery__actions {\n  min-width: 130px;\n  padding-left: 20px;\n  margin-left: 20px;\n  border-left: 1px solid var(--color-grey-light);\n  font-size: 14px;\n}\n\n.ck-gallery__pager {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 5px;\n}\n\n.ck-gallery__dots {\n  margin-right: 10px;\n  display: flex;\n}\n\n.ck-gallery__dot-item {\n  display: inline-block;\n  height: 20px;\n  width: 20px;\n  border-radius: 100%;\n  margin-right: 5px;\n  background-color: var(--color-black-30);\n  text-align: center;\n  line-height: 20px;\n  color: var(--color-white);\n  cursor: pointer;\n  font-size: 12px;\n  transition: background-color 0.35s ease;\n}\n\n.ck-gallery__add {\n  display: inline-block;\n  box-sizing: border-box;\n  height: 20px;\n  width: 20px;\n  border-radius: 100%;\n  margin-right: 5px;\n  margin-left: 10px;\n  background-color: var(--color-white);\n  text-align: center;\n  color: var(--color-blue);\n  line-height: 16px;\n  font-size: 16px;\n  cursor: pointer;\n  transition: background-color 0.35s ease;\n  border: 1px solid var(--color-blue);\n}\n\n.ck-gallery__dot-item.active {\n  background-color: var(--color-black-80);\n}\n\n\n.ck-gallery__add-slide:hover {\n  background-color: var(--color-blue);\n  color: var(--color-white);\n}\n\n.ck-gallery__icons {\n  max-width: 110px;\n  display: flex;\n  justify-content: space-between;\n  margin-top: 5px;\n}\n\n.ck-gallery__icon {\n  cursor: pointer;\n  width: 30px;\n  height: 30px;\n  border-radius: 50%;\n  transition: background-color 0.35s ease;\n  background-color: transparent;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.ck-gallery__icon:hover {\n  background-color: #f0f0f0;\n}\n\n.ck-gallery__icon > * {\n  width: 14px;\n  height: 14px;\n}\n\n[data-tooltip] {\n  position: relative;\n  cursor: pointer;\n  text-align: center;\n}\n\n/* Base styles for the entire tooltip */\n[data-tooltip]:before,\n[data-tooltip]:after {\n  position: absolute;\n  visibility: hidden;\n  transition:\n        opacity 0.2s ease-in-out,\n        visibility 0.2s ease-in-out,\n        transform 0.2s cubic-bezier(0.71, 1.7, 0.77, 1.24);\n  transform: translate3d(0, 0, 0);\n  pointer-events: none;\n}\n\n.disabled[data-tooltip]:before,\n.disabled[data-tooltip]:after {\n  display: none;\n}\n\n/* Show the entire tooltip on hover and focus */\n[data-tooltip]:hover:before,\n[data-tooltip]:hover:after,\n[data-tooltip]:focus:before,\n[data-tooltip]:focus:after {\n  visibility: visible;\n  opacity: 1;\n}\n\n/* Base styles for the tooltip's directional arrow */\n[data-tooltip]:before {\n  z-index: 1001;\n  border: 6px solid transparent;\n  background: transparent;\n  content: \"\";\n}\n\n/* Base styles for the tooltip's content area */\n[data-tooltip]:after {\n  z-index: 1000;\n  padding: 8px;\n  display: inline-block;\n  width: 160px;\n  background-color: var(--color-black-60);\n  color: var(--color-white);\n  content: attr(data-tooltip);\n  font-size: 13px;\n  line-height: 1;\n}\n\n/* Horizontally align top/bottom tooltips */\n[data-tooltip]:after {\n  margin-left: -80px;\n}\n\n[data-tooltip]:hover:before,\n[data-tooltip]:hover:after,\n[data-tooltip]:focus:before,\n[data-tooltip]:focus:after {\n  transform: translateY(-12px);\n}\n\n\n[data-tooltip]:before,\n[data-tooltip]:after {\n  top: 80%;\n  bottom: auto;\n  left: 50%;\n}\n\n[data-tooltip]:before {\n  margin-top: -12px;\n  margin-left: -6px;\n  margin-bottom: 0;\n  border-top-color: transparent;\n  border-bottom-color: var(--color-black-60);\n}\n\n[data-tooltip]:hover:before,\n[data-tooltip]:hover:after,\n[data-tooltip]:focus:before,\n[data-tooltip]:focus:after {\n  transform: translateY(12px);\n}\n\n"